import { AsyncDuckDB } from "../lib/duckdb"
import Block from "./ui/Block"
import { v4 as uuid } from "uuid"
import { Cell } from "../lib/utils"
import { uploadQueryBuilder } from "../lib/utils"
import { useState } from "react"

const INPUT_TABLE = "InputTable"

export default function Upload({
  db,
  updateQuery,
  cell,
}: {
  db: AsyncDuckDB | null
  updateQuery: (q: string) => void
  cell: Cell
}) {
  const handleFileUpload = async (event: any) => {
    const file = event.target.files[0]
    const fileName = file.name
    if (!file) {
      console.error("No file selected.")
      return
    }

    if (!db) {
      console.error("Database not initialized.")
      return
    }

    const reader = new FileReader()
    reader.onload = async (e) => {
      const csvContent = e.target?.result
      if (typeof csvContent !== "string") {
        console.error("File content is not a string.")
        return
      }
      try {
        const c = await db.connect()
        await db.registerFileText(fileName, csvContent)
        if (cell.query) {
          await c.query(`DROP TABLE "${INPUT_TABLE}"`)
        }
        await c.insertCSVFromPath(fileName, {
          schema: "main",
          name: INPUT_TABLE,
          detect: true,
        })
        await c.close()
        updateQuery(uploadQueryBuilder(INPUT_TABLE))
      } catch (error) {
        console.error("Error uploading CSV to DuckDB:", error)
      }
    }
    reader.readAsText(file)
  }

  return (
    <Block>
      <input type="file" accept=".csv" onChange={handleFileUpload} />
    </Block>
  )
}
