import React, { useState, useEffect } from "react"
import { AsyncDuckDB } from "../lib/duckdb"
import {
  Select,
  SelectTrigger,
  SelectContent,
  SelectItem,
  SelectValue,
} from "./ui/select"
import Block from "./ui/Block"
import TextInput from "./ui/textinput" // Adjust the path as necessary
import {
  Cell,
  query,
  mergeQueries,
  getFieldsQueryBuilder,
  getFieldValuesQueryBuilder,
  filterQueryBuilder,
} from "../lib/utils"
// @ts-ignore
const Filter = ({ filter, onFilterChange, onFilterDelete, db, accQuery }) => {
  const [fieldValues, setFieldValues] = useState([])
  useEffect(() => {
    const fetchFieldValues = async () => {
      if (db && filter.column) {
        const { rowsJson } = await query(
          db,
          mergeQueries(accQuery, getFieldValuesQueryBuilder(filter.column))
        )
        setFieldValues(rowsJson.map((row: any) => row[filter.column]))
      }
    }
    fetchFieldValues()
  }, [db, accQuery, filter.column])

  useEffect(() => {
    if (filter.column) {
      const defaultOperator = filter.fieldTypes
        .get(filter.column)
        .includes("Float")
        ? "="
        : "equals"
      onFilterChange({ ...filter, operator: defaultOperator, value: null })
    }
  }, [filter.column])

  return (
    <Block>
      <Select
        value={filter.column || null}
        onValueChange={(value) =>
          onFilterChange({
            ...filter,
            column: value,
            operator: null,
            value: null,
          })
        }
      >
        <SelectTrigger>
          <SelectValue placeholder="Select a field" />
        </SelectTrigger>
        <SelectContent>
          {/* @ts-ignore */}
          {filter.fields.map((field) => (
            <SelectItem key={field} value={field}>
              {field}
            </SelectItem>
          ))}
        </SelectContent>
      </Select>
      <Select
        value={filter.operator || null}
        onValueChange={(value) =>
          onFilterChange({ ...filter, operator: value })
        }
      >
        <SelectTrigger>
          <SelectValue placeholder="Select an operator" />
        </SelectTrigger>
        <SelectContent>
          {filter.fieldTypes.get(filter.column)?.includes("Float") && (
            <>
              <SelectItem value="=">&#61;</SelectItem>
              <SelectItem value=">=">&#62;&#61;</SelectItem>
              <SelectItem value="<=">&#60;&#61;</SelectItem>
              <SelectItem value=">">&#62;</SelectItem>
              <SelectItem value="<">&#60;</SelectItem>
            </>
          )}
          {filter.fieldTypes.get(filter.column)?.includes("Utf8") && (
            <>
              <SelectItem value="equals">Equals</SelectItem>
              <SelectItem value="contains">Contains</SelectItem>
              <SelectItem value="startsWith">Starts with</SelectItem>
              <SelectItem value="endsWith">Ends with</SelectItem>
            </>
          )}
        </SelectContent>
      </Select>
      {filter.operator !== "equals" ? (
        <TextInput
          setFieldValue={(value) => onFilterChange({ ...filter, value })}
        />
      ) : (
        <Select
          // value={filter.value || null}
          value={filter.value ?? undefined}
          onValueChange={(value) => onFilterChange({ ...filter, value })}
        >
          {/* <SelectTrigger>
            <SelectValue placeholder="Select a value" />
          </SelectTrigger> */}
          <SelectTrigger>
            <SelectValue
              placeholder={filter.value === null ? "Select a value" : undefined}
            />
          </SelectTrigger>

          <SelectContent>
            {fieldValues.map((value, i) => (
              <SelectItem key={i} value={String(value)}>
                {String(value)}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      )}
      <button
        className="ml-2 px-3 py-2 bg-red-500 text-white rounded-md"
        onClick={onFilterDelete}
      >
        Delete
      </button>
    </Block>
  )
}

const FilterGroup = ({
  filterGroup,
  onFilterGroupChange,
  onFilterGroupDelete,
  indent = 0,
  db,
  accQuery,
}: {
  filterGroup: any
  onFilterGroupChange: any
  onFilterGroupDelete: any
  indent?: number
  db: any
  accQuery: any
}) => {
  const [fields, setFields] = useState<any[]>([])
  const [fieldTypes, setFieldTypes] = useState<Map<any, any>>(new Map())
  useEffect(() => {
    const fetchFields = async () => {
      if (db && accQuery) {
        const { result, rowsJson } = await query(
          db,
          mergeQueries(accQuery, getFieldsQueryBuilder())
        )
        setFields(Object.keys(rowsJson[0]))
        const types = new Map(
          result.schema.fields.map((f: any) => [f.name, String(f.type)])
        )
        setFieldTypes(types)
        //console.log("asdasd", types)
      }
    }
    fetchFields()
  }, [db, accQuery])

  const handleAddFilter = () => {
    const newFilter = {
      column: fields[0],
      operator: null,
      value: "",
      fields: fields,
      fieldTypes: fieldTypes,
    }
    onFilterGroupChange({
      ...filterGroup,
      children: [...filterGroup.children, newFilter],
      boolOperators: [...filterGroup.boolOperators, "AND"],
    })
  }

  const handleAddFilterGroup = () => {
    const newFilterGroup = { children: [], boolOperators: [] }
    onFilterGroupChange({
      ...filterGroup,
      children: [...filterGroup.children, newFilterGroup],
      boolOperators: [...filterGroup.boolOperators, "AND"],
    })
  }
  //@ts-ignore
  const handleFilterChange = (index, newFilter) => {
    const updatedChildren = [...filterGroup.children]
    updatedChildren[index] = newFilter
    onFilterGroupChange({ ...filterGroup, children: updatedChildren })
  }
  //@ts-ignore
  const handleFilterDelete = (index) => {
    //@ts-ignore
    const updatedChildren = filterGroup.children.filter((_, i) => i !== index)
    const updatedBoolOperators = filterGroup.boolOperators.filter(
      //@ts-ignore
      (_, i) => i !== index
    )
    onFilterGroupChange({
      ...filterGroup,
      children: updatedChildren,
      boolOperators: updatedBoolOperators,
    })
  }
  //@ts-ignore
  const handleBoolOperatorChange = (index, newOperator) => {
    const updatedBoolOperators = [...filterGroup.boolOperators]
    updatedBoolOperators[index] = newOperator
    onFilterGroupChange({ ...filterGroup, boolOperators: updatedBoolOperators })
  }

  return (
    <div style={{ marginLeft: `${indent * 10}px` }}>
      <Block>
        <button
          className="px-3 py-2 bg-blue-500 text-white rounded-md"
          onClick={handleAddFilter}
        >
          Add Filter
        </button>
        <button
          className="ml-2 px-3 py-2 bg-blue-500 text-white rounded-md"
          onClick={handleAddFilterGroup}
        >
          Add Filter Group
        </button>
        {onFilterGroupDelete && (
          <button
            className="ml-2 px-3 py-2 bg-red-500 text-white rounded-md"
            onClick={onFilterGroupDelete}
          >
            Delete Group
          </button>
        )}
      </Block>
      {/* @ts-ignore */}
      {filterGroup.children.map((child, index) => (
        <div key={index} style={{ marginLeft: "20px" }}>
          {child.column ? (
            <Filter
              filter={child}
              //@ts-ignore
              onFilterChange={(newFilter) =>
                handleFilterChange(index, newFilter)
              }
              onFilterDelete={() => handleFilterDelete(index)}
              db={db}
              accQuery={accQuery}
            />
          ) : (
            <FilterGroup
              filterGroup={child}
              //@ts-ignore
              onFilterGroupChange={(newFilterGroup) =>
                handleFilterChange(index, newFilterGroup)
              }
              onFilterGroupDelete={() => handleFilterDelete(index)}
              indent={indent + 1}
              db={db}
              accQuery={accQuery}
            />
          )}
          {index < filterGroup.children.length - 1 && (
            <Select
              value={filterGroup.boolOperators[index]}
              onValueChange={(value) => handleBoolOperatorChange(index, value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select a boolean operator" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="AND">AND</SelectItem>
                <SelectItem value="OR">OR</SelectItem>
              </SelectContent>
            </Select>
          )}
        </div>
      ))}
    </div>
  )
}
//@ts-ignore
const constructSQL = (filterGroup) => {
  let sqlParts: string[] = []
  //@ts-ignore
  filterGroup.children.forEach((child, index) => {
    if (child.column) {
      // It's a filter
      let operatorStr = child.operator
      if (operatorStr === "equals") operatorStr = "="
      if (operatorStr === "contains") operatorStr = "LIKE"
      if (operatorStr === "startsWith") operatorStr = "LIKE"
      if (operatorStr === "endsWith") operatorStr = "LIKE"
      let valueStr = child.value
      if (operatorStr === "LIKE" && child.operator === "contains")
        valueStr = `'%${valueStr}%'`
      if (operatorStr === "LIKE" && child.operator === "startsWith")
        valueStr = `'${valueStr}%'`
      if (operatorStr === "LIKE" && child.operator === "endsWith")
        valueStr = `'%${valueStr}'`
      else valueStr = `'${valueStr}'`

      console.log("as", valueStr)
      if (valueStr === "null") {
        sqlParts.push(`(${child.column} IS asdasdaNULL)`)
      } else {
        sqlParts.push(`(${child.column} ${operatorStr} ${valueStr})`)
      }
    } else {
      // It's a filter group
      sqlParts.push(constructSQL(child))
    }

    // Append the boolean operator except for the last child
    if (index < filterGroup.children.length - 1) {
      sqlParts.push(filterGroup.boolOperators[index])
    }
  })

  return sqlParts.join(" ")
}

export const FilterBlock = ({
  db,
  updateQuery,
  prevQuery,
}: {
  db: AsyncDuckDB | null
  updateQuery: (q: string) => void
  prevQuery: string
}) => {
  const [rootFilterGroup, setRootFilterGroup] = useState({
    children: [],
    boolOperators: [],
  })

  useEffect(() => {
    if (db) {
      const q = mergeQueries(
        prevQuery,
        filterQueryBuilder(constructSQL(rootFilterGroup))
      )
      updateQuery(q)
    }
  }, [db, rootFilterGroup, prevQuery])

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Filter Builder</h1>
      <FilterGroup
        filterGroup={rootFilterGroup}
        onFilterGroupChange={setRootFilterGroup}
        db={db}
        accQuery={prevQuery}
        onFilterGroupDelete={() => {}}
      />
      <div className="mt-4 font-bold">
        <span>SQL WHERE Clause:</span>
        <p className="w-96">{constructSQL(rootFilterGroup)}</p>
      </div>
    </div>
  )
}

// write code to default export FilterBlock
export default FilterBlock
