import { useEffect, useState } from "react"
import Upload from "./components/Upload"
import FilterBlock from "./components/FilterBlock"
import Columns from "./components/Columns"
import TableView from "./components/TableView"
import Download from "./components/Download"
import {
  ResizableHandle,
  ResizablePanel,
  ResizablePanelGroup,
} from "./components/ui/resizable"
import { initDb, AsyncDuckDB } from "./lib/duckdb"
import { Button } from "./components/ui/button"
import { Cell, CellType } from "./lib/utils"

const addCell = (
  type: CellType,
  setCells: React.Dispatch<React.SetStateAction<Cell[]>>
) => {
  setCells((prevCells) => [...prevCells, { type }])
}

const updateQueryFactory = (
  i: number,
  cell: Cell,
  setCells: React.Dispatch<React.SetStateAction<Cell[]>>
) => {
  return (q: string) => {
    setCells((prev) => [
      ...prev.slice(0, i),
      { ...cell, query: q },
      ...prev.slice(i + 1),
    ])
  }
}

export default function App() {
  const [db, setDb] = useState<AsyncDuckDB | null>(null)
  const [cells, setCells] = useState<Cell[]>([{ type: "upload" }])
  useEffect(() => {
    const initDbAsync = async () => {
      const db = await initDb()
      setDb(db)
    }
    initDbAsync()
  }, [])
  return (
    <ResizablePanelGroup direction="horizontal" className="rounded-lg border">
      <ResizablePanel key={123} defaultSize={50}>
        <div className="flex flex-col justify-center items-center">
          {cells?.map((cell, i) => {
            if (cell.type === "upload") {
              return (
                <Upload
                  key={i}
                  db={db}
                  updateQuery={updateQueryFactory(i, cell, setCells)}
                  cell={cell}
                />
              )
            } else if (cell.type === "filter") {
              return (
                <FilterBlock
                  key={i}
                  db={db}
                  updateQuery={updateQueryFactory(i, cell, setCells)}
                  prevQuery={cells[i - 1].query as string}
                />
              )
            } else if (cell.type === "table") {
              return (
                <div className="mt-2 flex justify-center items-start border rounded-lg max-w-full max-h-[25vh] overflow-y-scroll">
                  <TableView
                    key={i}
                    db={db}
                    updateQuery={updateQueryFactory(i, cell, setCells)}
                    prevQuery={cells[i - 1].query as string}
                    rowAmount={100}
                  />
                </div>
              )
            } else if (cell.type === "columns") {
              return (
                <Columns
                  key={i}
                  db={db}
                  updateQuery={updateQueryFactory(i, cell, setCells)}
                  prevQuery={cells[i - 1].query as string}
                />
              )
            } else if (cell.type === "download") {
              return (
                <Download
                  key={i}
                  db={db}
                  prevQuery={cells[i - 1].query as string}
                />
              )
            }
            return null
          })}
          {cells[cells.length - 1].query && (
            <div className="mt-2 flex items-center">
              <Button onClick={() => addCell("filter", setCells)}>
                Filter
              </Button>
              <Button
                onClick={() => addCell("table", setCells)}
                className="ml-2"
              >
                Table
              </Button>
              <Button
                onClick={() => addCell("columns", setCells)}
                className="ml-2"
              >
                Columns
              </Button>
              <Button
                onClick={() => addCell("download", setCells)}
                className="ml-2"
              >
                Download
              </Button>
            </div>
          )}
        </div>
      </ResizablePanel>
      <ResizableHandle withHandle />
      <ResizablePanel
        key={456}
        style={{ maxHeight: "100vh", overflowY: "auto" }}
      >
        <TableView
          key="placeholder123"
          db={db}
          updateQuery={() => {}}
          prevQuery={cells[cells.length - 1].query as string}
          rowAmount={100}
        />
      </ResizablePanel>
    </ResizablePanelGroup>
  )
}
