import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table"
import { AsyncDuckDB } from "../lib/duckdb"
import { useEffect, useState } from "react"
import { Cell, query, tableViewQueryBuilder, mergeQueries } from "../lib/utils"

export default function TableView({
  db,
  updateQuery,
  prevQuery,
  rowAmount = 100,
}: {
  db: AsyncDuckDB | null
  updateQuery: (q: string) => void
  prevQuery: string
  rowAmount: number
}) {
  const [rows, setRows] = useState<any[]>()
  useEffect(() => {
    if (db) {
      const fetch = async () => {
        const q = mergeQueries(prevQuery, tableViewQueryBuilder(rowAmount))
        const { rowsJson } = await query(db, q)
        setRows(rowsJson)
        updateQuery(prevQuery)
      }
      fetch()
    }
  }, [db, prevQuery])

  if (!rows?.length) {
    return <div>No data / Zero rows returned</div>
  }

  return (
    <Table>
      <TableHeader>
        <TableRow>
          <TableHead key={"index"}>Index</TableHead>
          {rows &&
            Object.keys(rows[0]).map((key) => (
              <TableHead key={key}>{key}</TableHead>
            ))}
        </TableRow>
      </TableHeader>
      <TableBody>
        {rows.map((row: any, index: number) => (
          <TableRow key={index}>
            <TableCell key={"index"}>{index}</TableCell>
            {rows &&
              Object.keys(row).map((key) => (
                <TableCell key={key}>{row[key]}</TableCell>
              ))}
          </TableRow>
        ))}
      </TableBody>
    </Table>
  )
}
